import { Component, OnInit, Input } from '@angular/core';
// import { PartyRelationBean } from './bean/party-relation.bean';
import { CommonHttpService } from '../../http/common.http.service';
import { ConfirmationService, TreeNode } from 'primeng/primeng';
import { AdministerTreeBean } from './bean/administer-tree.bean'
// import { LegalFlag, OperatingLevel, OrgTypes, OrgStatus } from '../../constant/common.codeValue';
import { Message } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun'
@Component({
  selector: 'administer-tree',
  templateUrl: 'administer-tree.html',
  styleUrls: ['administer-tree.scss'],
  providers: [CommonHttpService, Commfunc]
})

/**
 * 
 */
export class AdministerTree implements OnInit {

  precredit: string = '';
  precredit1: string = '';
  //
  msgs: Message[] = [];
  aa: boolean = false;
  legalFlag: any = [];
  operatingLevel: any = [];
  orgType: any = [];
  orgStatus: any = [];
  bb: string = ''
  test: any = [];
  isExpand: boolean
  treeList: string = '';
  treeList2: string = '';//
  treeList1: string = '';//
  choiceOrgName: string;
  orgName: string;
  orgId: string;
  aalist: any = [];
  administerTreeBean: AdministerTreeBean = new AdministerTreeBean()
  listData: any
  list = {
    orgId: '',
    superiorManageOrg: '',
    orgName: '',
    orgType: '',
    operatingLevel: '',
    orgStatus: '',
    roleType: '',
    legalFlag: '',
    location: ''

  } //
  files: TreeNode[];
  // treeList2:string;

  nodeNum = 0;
  //
  anchorId: string;
  //
  list1: any = [];
  code: any;
  constructor(
    public httpService: CommonHttpService,
    public commfunc: Commfunc
  ) {
    this.codeValues() //
    //,
    this.legalFlag = this.code['LegalFlag'];
    this.operatingLevel = this.code['OperatingLevel'];
    this.orgType = this.code['OrgTypes'];
    this.orgStatus = this.code['OrgStatus']
  }

  ngOnInit() {
    // this.query()
    this.administerTreeBean.rowNum = '10';
    this.administerTreeBean.startRow = '1'

  }
  //
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code
  }
  ngOnChanges() {
    this.confirmPosition()
  }
  choceLegalFlag() {

    if (this.administerTreeBean.legalFlag == '1') {
      let param = { orgId: 'T0000' }
      this.queryCzBank(param)
    }
    if (this.administerTreeBean.legalFlag == '2') {
      let param = { orgId: 'C0000' }
      this.queryCzBank(param)
    }
  }

  //
  queryCzBank(param) {
    this.httpService.queryOrgTreeList(param).subscribe(data => {
      this.treeList1 = JSON.stringify(this.forMenuList(data.orgList));
      this.treeList = this.treeList1
      this.aalist = this.forMenuList(data.orgList);
      this.listToLabelvalue(data.orgList, 'orgId', 'orgName');
      if (this.treeList || this.treeList1) {
        // this.msgs = [];
        // this.msgs.push({ severity: 'success', summary: '', detail: '' });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
      }
    })
  }

  //
  query() {
    this.httpService.queryOrgById(this.administerTreeBean).subscribe(data => {
      this.listData = data
      this.list.orgId = this.listData.orgId
      this.list.superiorManageOrg = this.listData.superiorManageOrg
      this.list.orgName = this.listData.orgName
      this.list.orgType = this.listData.orgType
      this.list.operatingLevel = this.listData.operatingLevel
      this.list.orgStatus = this.listData.orgStatus
      this.list.roleType = this.listData.roleType
      this.list.location = this.listData.location
      this.list.legalFlag = this.listData.legalFlag

    })
  }


  treeClick(event) {
    this.administerTreeBean = new AdministerTreeBean()
    this.administerTreeBean.orgId = event.data
    this.administerTreeBean.orgName = event.label
    this.administerTreeBean.orgName = event.label
    this.query()
  }

  // 
  open() {
    if (this.aa == false) {
      this.aa = true;
    }

  }
  //
  close() {
    if (this.aa == true) {
      this.aa = false;
    }
  }
  //
  listToLabelvalue(data: any = [], label, value) {
    if (data) {
      data.forEach(item => {
        let tempObj = {};
        tempObj['label'] = item[label];
        tempObj['value'] = item[value];
        this.list1.push(tempObj)
        if (item['subOrg'] && item['subOrg'].length > 0) {
          this.listToLabelvalue(item['subOrg'], label, value)
        }
      });
    }
  }
  //
  checkMenuList(param: Object) {
    if (param['subOrg'] && param['subOrg'].length > 0) {
      return true;
    }
    return false;
  }

  forMenuList(param: Array<any>) {
    let list: any = [];
    if (!param) {
      return;
    }
    param.forEach(element => {
      let el = { label: element.orgName, data: element.orgId, children: [] };
      if (this.checkMenuList(element)) {
        el['expandedIcon'] = 'fa-folder-open';
        el['collapsedIcon'] = 'fa-folder';
        el['icon'] = 'fa-file-video-o';
        el['children'] = this.forMenuList(element['subOrg']);
      }
      list.push(el)
    })
    return list;
  }
  //onchange
  doChose(event) {
    if (/\w/.test(event.value)) {
      this.administerTreeBean.orgName = this.returnValue(event.value, 'label', 'value');
    } else {
      this.administerTreeBean.orgName = event.value;
    }

  }
  /**
     * 
     * @param param  
     * @param colum  
     */
  returnValue(param, colum, valueColum) {
    let value: string = '';
    this.list1.forEach(element => {
      if (element[colum] === param) {
        value = element[valueColum];
      }
    });
    return value;
  }

  //
  confirmPosition() {
    this.choiceOrgName = this.administerTreeBean.orgName
    this.aa = true;
    //
    setTimeout(() => {
      this.position(this.choiceOrgName);
    }, 1)
  }
  //
  position(a) {
    let location = document.getElementById('location');
    // let ele1=document.querySelectorAll('.ui-treenode-leaf')
    // let ele=document.querySelector('.true')
    let ele = location.getElementsByTagName('span');
    let arr = [];
    for (const i in ele) {
      if (ele[i].innerText.match(a) != null) {
        arr.push(ele[i]);
        break;
      }
    }
    let top = arr[0].offsetTop;
    // let top=ele.offsetTop;
    let parent = arr[0].offsetParent
    // let parent=ele[0].offsetParent;
    while (parent != null) {
      top += parent.offsetTop;
      parent = parent.offsetParent
    }
    location.scrollTop = top - 500;
  }

  //
  reset() {
    this.administerTreeBean = new AdministerTreeBean()
    this.orgId = "";
  }

  getNodeNum(data) {
    this.nodeNum = data;
  }
}